.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eceff1;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-left: 5px;
}
.header__middle {
  display: flex;
  flex: 0.7;
  align-items: center;
  background: #f1f3f4;
  padding: 5px 10px;
  border-radius: 8px;
}
.header__middle > .MuiSvgIcon-root {
  color: #5f6368;
}
.header__middle > input {
  border: none;
  width: 100%;
  padding: 10px;
  outline-width: 0;
  background: transparent;
}
.header__right {
  display: flex;
  padding-right: 20px;
}
